function removeFnNoise(fn) {
  if (/^function /.test(fn)) {
    fn = fn.substring(9);
  }

  return fn.replace(/[_]typeof/g, 'typeof');
}

function removeNoise(str) {
  return str.replace(/^[A-Za-z]+/, 'function').replace(/[_]typeof/g, 'typeof');
}

